import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { JobApplicationService } from 'app/data/jobApplication/jobApplication.service';
import { ActivatedRoute, Router } from '@angular/router';
import { StorageService } from 'app/providers/storage/storage.service';

@Component({
  selector: 'app-update-job-application',
  templateUrl: './update-job-application.component.html',
  styleUrls: ['./update-job-application.component.css']
})
export class UpdateJobApplicationComponent implements OnInit {
  private appdata : any;
  jobApplicationSubscriber: any;
  userInfo: any;
  username;
  status : string[];
  updateJobApplicationForm: FormGroup;

  constructor(public fb: FormBuilder,private jobApplicationService: JobApplicationService,  public router :Router,private route : ActivatedRoute, private storageServive: StorageService) { 
    
    this.status = ["waiting","confirm","rejected"];
    

    this.jobApplicationSubscriber = this.jobApplicationService.jobApplicationSubscriber().subscribe((data: any) => {
      this.jobApplicationSubscriber = data ? data.data : undefined;
      
      this.username = this.storageServive.get('userInfo').user.first_name.charAt(0).toUpperCase() + this.storageServive.get('userInfo').user.first_name.slice(1) + " " + this.storageServive.get('userInfo').user.last_name;
      console.log(this.userInfo);

    })
   }
  ngOnInit(): void
   {
      this.updateJobApplicationForm = this.fb.group({
        updated_status: [''],
      })
      const id = this.route.snapshot.paramMap.get('id');
      this.jobApplicationService.getApplication(id).subscribe((data:any) =>{
        this.appdata = data ? data.data : undefined;
      });
    
    }

    updateForm() {
      //when the form is submitted
      //this method will be invoked
      //update will change only status
      // let check = document.getElementById("newStatus").value;
      // let check = document.getElementById("newStatus").value;
      let check = this.updateJobApplicationForm.value.updated_status;
      console.log("JS Check" + check);
      console.log(this.updateJobApplicationForm.value);
      this.updateJobApplicationForm.value._id = this.route.snapshot.paramMap.get('id');
      this.updateJobApplicationForm.value.application_status = check;
      console.log(this.updateJobApplicationForm.value);
      // alert(this.updateJobApplicationForm.value.application_status);
      this.jobApplicationService.updateJobApplication(this.updateJobApplicationForm.value).subscribe(jobApplication => {
     this.router.navigate(['/'+this.userInfo.user.role.toLowerCase()+'/my-applications'])
     });
     this.router.navigate(['/'+this.userInfo.user.role.toLowerCase()+'/my-applications'])

    }
    ngOnDestroy(){
      // this.jobSubscriber.unsubscribe();
    }
}
